.Categories{
	
	h1{
		font-weight: 450;
	}
	display: flex;
	flex-direction: column;
	// height: 0vh;
	min-height: 60vh;
	// row-gap:.2cm;
	align-items: center;
}
.categories-head{
	font-size: 50px;
	// font-weight: 700;
	
	font-family: Inika;
	// border-right: .15em solid orange;
	// letter-spacing: .07cm;
	// line-height: 1.2cm;
	// color: blueviolet;
	overflow: hidden;
	white-space: nowrap;
	animation: typing 4s steps(40, end);

}
@keyframes typing {
	0% { width: 0 }
	15% {width: 15%;}
	30% {width: 30%;}
	45% {width: 45%;}
	60% {width: 60%;}
	75% {width: 75%;}
	90% {width: 90%;}
	100% { width: 100% }
  }
.choose{
	font-size: 20px;
	font-family: inika;
}
.categories-container{
	
	display: flex;
	column-gap: .6cm;
	row-gap:.6cm;
	flex-wrap: wrap;
	
	justify-content: center;
	align-items: flex-start;
	
}
.category{
	width: 7cm;
	// background-color: #c8a7f1;
	background-color: #D09CFA;
	border-radius: 5px;
	color: #28282b;
	cursor: pointer;
	font-weight: 600;
	// transition: .3s;
	box-shadow: rgba(0, 0, 0, 0.16) 0px 3px 6px, rgba(0, 0, 0, 0.23) 0px 3px 6px;
	
}
.category:hover{
	box-shadow: rgba(2, 0, 0, 0.978) 0px 0px 0px 2px;
}
.cat-img{
	width: 5cm;
	height: 5cm;
	transition: .3s;
}

.category-content{
	padding: 1vh 3vh 1vh 3vh;
	h1{
		font-size: 30px;
		margin: 0px 0px;
		font-weight: 500;
		transition: .3s;
		// font-weight: 600;
		font-family: inika;
		color: #000000;
		// letter-spacing: .03cm;
	}
	p{
		font-weight: 500;
	}
	
}
.category-link{
	text-decoration: none;
	color: whitesmoke;
}
