.Team{
    min-height: 60vh;
}
.t-img{
border-radius: 100px;
}
.team-container{
    width: fit-content;
    margin-top: 2cm;
    padding: .5cm 0.5cm .5cm 0.5cm;
	background-color: #9af0a3;
    box-shadow: rgba(0, 0, 0, 0.16) 0px 3px 6px, rgba(0, 0, 0, 0.23) 0px 3px 6px;
    border-radius: 10px;
	display: flex;
	flex-direction: column;
	align-items: center;
    // border: 1px solid black;
}


.team-name{
    font-family: Inika;
    // font-weight: 500;
}

// .team-cat{
//     border-radius: 50px;
// }
.team-cat:hover{
    transform: scale(1);
}
.fill-form{
   
    color: black;
}
.team-cat-container{
    display: flex;
    flex-direction: column;
    row-gap: .3cm;
   
}
.socials{
    display: flex;

    column-gap: .2cm;
    justify-content: center;
}
.team-cat-container:hover{
    .social-team{
         display: flex;
         flex-direction: column;
    column-gap: .2cm;
    justify-content: center;
    opacity: 1;
    }
}
.port{
   width: auto;
   height: auto;
   transform: scale(1.05);
}
.social-team{
    transition: .7s;
    display: none;
    opacity: 0;
    // z-index: -1;
    // display: flex;
    // column-gap: .2cm;
    // justify-content: center;
}

