@media screen and (max-width:600px){
    .team-post{
        font-size: 20px;
    }
}
@media screen and (max-width:500px){
    .team-container{
        width: 80vw;
    }
}
@media screen and (max-width:400px){
    .team-post{
        font-size: 14px;
    }
    .team-container{
        width: 70vw;
    }
}