.privacy-head{
    font-size: 50px;
    font-weight: 500;
}
.privacy-tags{
    font-weight: 500;
}
.privacy-tag{
    font-weight: 500;
}


@media screen and (max-width:900px){
    .privacy-head{
        font-size: 45px;
    }
    .privacy-tag{
        font-size: 30px;
    }
}
@media screen and (max-width:800px){
    .privacy-head{
        font-size: 40px;
    }
    .privacy-tag{
        font-size: 25px;
    }
}
@media screen and (max-width:600px){
    .privacy-head{
        font-size: 35px;
    }
    .privacy-tag{
        font-size: 20px;
    }
    .privacy-tags{
        font-size: 20px;
    }
}
@media screen and (max-width:500px){
    .privacy-head{
        font-size: 30px;
    }
    .privacy-tag{
        font-size: 18px;
    }
    .privacy-tags{
        font-size: 20px;
    }
    .privacy-text{
        font-size: 14px;
    }
}
@media screen and (max-width:400px){
    .privacy-head{
        font-size: 28px;
    }
    .privacy-tag{
        font-size: 18px;
    }
    .privacy-tags{
        font-size: 20px;
    }
    .privacy-text{
        font-size: 14px;
    }
}
@media screen and (max-width:300px){
    .privacy-head{
        font-size: 25px;
    }
    .privacy-tag{
        font-size: 17px;
    }
    .privacy-tags{
        font-size: 20px;
    }
    .privacy-text{
        font-size: 13px;
    }
}