.SignUp{
    min-height: 60vh;
    display: flex;
    justify-content: center;
    // padding-top: 10vh;
}
.signup-container{
    // padding-top: 10vh;
    display: flex;
    // flex-direction: column;
    justify-content: center;
    align-items: center;
    column-gap: 2cm;
}
.signin-box{
    width: 10cm;
    height: auto;
    // border: 1px solid black;
    // background-color: #dc9adc;
    background-color: #ff96ce;
    box-shadow: rgba(0, 0, 0, 0.16) 0px 3px 6px, rgba(0, 0, 0, 0.23) 0px 3px 6px;
    border-radius: 10px;
    padding: 15px;
}
.login-p{
    font-size: 25px;
    font-family: raleway;
    font-weight: 600;
}
.why-signup{
    text-align: left;
}
.coll{
    font-weight: 600;
}
.sign-g{
    cursor: pointer;
    font-family: lora;
    // font-size: 16px;
    // color: #28282b;
    background-color: rgb(97, 97, 221);
    
    
    
}
.user-profile-img{
    border-radius: 50px;
    // width: 5cm;
    border: 2px solid #28282b;
  margin-left: .5cm;
}

.create{
    font-size: 35px;
    font-family: Inika;
    color: #28282b;
    // overflow: hidden;
	// white-space: nowrap;
    
    // animation: typing 4s steps(40, end);
    // font-weight: 600;
    // padding: 10px;

}
.welcome-user{
    font-size: 30px;
    font-family: raleway;
    color: #28282b;
    
    font-weight: 600;
}
.profile-data{
    display: flex;
    align-items: center;
    justify-content: center;
    column-gap: .3cm;
    background-color: #87f195;
    // background-color: #FFBBCC;
    width: auto;
    padding: .3cm 0cm .3cm 0cm;
    box-shadow: rgba(0, 0, 0, 0.16) 0px 3px 6px, rgba(0, 0, 0, 0.23) 0px 3px 6px;
    border-radius: 10px;
    // border: 2px solid #28282b;
}
.st{
    border: none;
    border-radius: 5px;
    font-size: 14px;
    background-color: rgb(228, 69, 69);
    color: whitesmoke;
    cursor: pointer;
    padding: 3px 5px 3px 5px;
    // box-shadow: rgba(0, 0, 0, 0.16) 0px 3px 6px, rgba(0, 0, 0, 0.23) 0px 3px 6px;
	box-shadow: rgba(0, 0, 0, 0.16) 0px 3px 6px, rgba(0, 0, 0, 0.23) 0px 3px 6px;

}
.st:hover{
    box-shadow: rgba(2, 0, 0, 0.978) 0px 0px 0px 1.5px;

}
.subs{
    font-size: 14px;
    font-weight: 600;
}
.name-section{
    display: flex;
    flex-direction: column;
    row-gap: .2cm;
    padding-right: .5cm;
}

@keyframes typing {
	0% { width: 0 }
	15% {width: 15%;}
	30% {width: 30%;}
	45% {width: 45%;}
	60% {width: 60%;}
	75% {width: 75%;}
	90% {width: 90%;}
	100% { width: 100% }
  }

.check-mark{
    width: .6cm;
    height: .6cm;
}
.benefit{
    display: flex;
    column-gap: .2cm;
    align-items: center;
   
}
.benefit-content{
    font-size: 22px;
    font-family: itim;
}
button:disabled,
button[disabled]{
    opacity: .6;
}
.checkbox-section{
    display: flex;
    align-items: baseline;
    column-gap: .1cm;
}
@media screen and (max-width:1200px){
    .signup-container{
        // padding-top: 5vh;
        flex-direction: column-reverse;
        align-items: center;
        row-gap: 1cm;
        
    }
}
@media screen and (max-width:1000px){
    .create{
        font-size: 30px;
    }
    
}
@media screen and (max-width:800px){
    .create{
        font-size: 25px;
    }
    
    .benefit-content{
        font-size: 20px;
    }
    .user-profile-img{
        width: 2cm;
    }
    
}
@media screen and (max-width:600px){
    
    .welcome-user{
        font-size: 25px;
    }
    .benefit-content{
        font-size: 18px;
    }
    .check-mark{
        width: .4cm;
        height: .4cm;
    }
    .benefit{
        column-gap: .1cm;
       
    }
    // .profile-data{
    //     width: 7cm;
    // }
    
}

@media screen and (max-width:500px){
   
    .welcome-user{
        font-size: 22px;
    }
    // .profile-data{
    //     width: 6.5cm;
    // }
    .signin-box{
        width: 75vw;
    }
    .subs{
        font-weight: 500;
    }
    
    
    
}
@media screen and (max-width:400px){
    .benefit-content{
        font-size: 16px;
    }
    .user-profile-img{
        width: 1.5cm;
    }
    
    .user-profile-img{
        width: 1.5cm;
    }
    // .profile-data{
    //     width: 5.7cm;
    // }
    .st{
        font-size: 12px;
    }
    .create{
        font-size: 20px;
    }
    
}
@media screen and (max-width:300px){
    .create{
        font-size: 18px;
    }
    .welcome-user{
        font-size: 20px;
    }
    // .profile-data{
    //     width: 5.4cm;
    // }
}
@media screen and (max-width:250px){
    .create{
        font-size: 16px;
    }
    .welcome-user{
        font-size: 18px;
    }
    // .profile-data{
    //     width: 5cm;
    // }
    
}