body {
  animation: theme 200s linear infinite;
  
  &:after,
  &:before {
      content: '';
      display: block;
      position: fixed;
      z-index: -1;
      top: 0;
      width: 100vw;  // IE/Edge
      height: 100vh; // fallback
      width: 100vmax;
      height: 100vmax;
      background: rgba(0,0,0,0.05);
      // background-color: white;
      animation: backgroundd 100s linear infinite;
  }

  &:after {
      left: 15vw;
  }

  &:before {
      right: 15vw;
      animation-delay: -30s;
      animation-direction: reverse;
  }
}

@keyframes theme {
  0% {
    background: #dff5f8;
  }
  
  16% {
      background: #d8f3ea;
  }
  
  33% {
      
      background: #f6f0dd
  }
  
  50% {
      background: #e9fce5;
  }
  
  66% {
      background: #f6f3df;
  }    
  
  83% {
      background: #e1ecf8;
      
  }

  100% {
    background: #dff5f8;
  }
}

@keyframes backgroundd {
  0% {
      transform: rotate(0deg);
  }
  
  100% {
      transform: rotate(360deg);
  }
}

::-webkit-scrollbar {
  width: 11px;
  height: 0px;
  cursor: pointer;


}



