
.reviews-total{
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    row-gap: .5cm;
    column-gap: .5cm;
    height: auto;
    max-height: 70vh;
    overflow-y: scroll;
    padding-top: .5cm;
    padding-bottom: .5cm;
    
      
}
.reviews-total::-webkit-scrollbar {
    width: 5px;
    /* for Chrome, Safari, and Opera */
  }
.review-box{
    width: auto;
    max-width: 80vw;
    // height: auto;
    box-shadow: rgba(0, 0, 0, 0.16) 0px 3px 6px, rgba(0, 0, 0, 0.23) 0px 3px 6px;
    background-color: #edd1f9;
    border-radius: 5px;
    // padding: .5cm;
}

.review-box-upper{
    display: flex;
    column-gap: .2cm;
    align-items: center;
    padding: .3cm .3cm .1cm .3cm ;
    // border-bottom: .5px solid gray;
}
.avatar-review{
    width: .8cm;
    border-radius: 50px;
}
.review-name{
    font-size: 18px;
    // font-weight: 600;
    // font-family: roboto;
}
.review-text{
    font-size:17px;
    // font-family: inika;
    padding: 0cm .5cm .3cm 1.3cm;
    text-align: left;
}
.rev-form{
    display: flex;
    flex-direction: column;
    align-items: center;
    row-gap: .1cm;
}

.create-account{
    height: 100%;
  width: 10cm;
  height: 4cm;
  position: fixed;
  z-index: 1;
  top: 5%;
  right: 40%;
  background-color: #28282b;
  overflow-y: hidden;
  transition: 0.5s;
  display: flex;
  flex-direction: column;
  align-items: center;
  box-shadow: rgba(60, 64, 67, 0.3) 0px 1px 2px 0px, rgba(60, 64, 67, 0.15) 0px 1px 3px 1px;
  border-radius: 5px;
}
.create-para{
    font-size: 25px;
    color: whitesmoke;
    padding: .5cm;
}
.b{
    padding: .5cm;
}
@media screen and (max-width:900px){
    .create-account{
       
      width: 8cm;
      height: 3.5cm;
      right: 30%;
    }
}
@media screen and (max-width:700px){
    .create-account{
       
      width: 7cm;
      height: 3.5cm;
      right: 30%;
    }
}
@media screen and (max-width:600px){
    .create-account{
       
      width: 6cm;
      height: 3cm;
      right: 25%;
    }
    .create-para{
        font-size: 22px;
        padding: .3cm;
    }
}
@media screen and (max-width:500px){
    .review-box{
        width: 80vw;
       
    }
    .review-text{
        font-size: 16px;
        padding: 0cm .2cm .2cm 1cm;
        
    }
    .review-name{
        font-size: 17px;
        
        
    }

    .review-box-upper{
        
        padding: .2cm;
    }
    .avatar-review{
        width: .6cm;
        border-radius: 50px;
    }
}
@media screen and (max-width:400px){
    .review-box{
        width: 80vw;
       
    }
    .review-text{
        font-size: 16px;
        padding: 0cm .2cm .2cm .8cm;
    }
    .review-name{
        font-size: 17px;
        
    }
    .avatar-review{
        width: .6cm;
        border-radius: 50px;
    }
    
    .create-account{
       
        width: 5cm;
        height: 2.5cm;
        right: 20%;
      }
      .create-para{
          font-size: 20px;
          padding: .2cm;
      }
}
@media screen and (max-width:300px){
    .review-text{
        font-size: 14px;
        padding: .2cm;
    }
    .review-name{
        font-size: 15px;
        
    }
    .create-account{
       
        width: 4.5cm;
        height: 2.2cm;
        right: 18%;
      }
      .create-para{
          font-size: 18px;
          padding: .2cm;
      }
}
@media screen and (max-width:200px){
    .review-text{
        font-size: 14px;
    }
    .review-name{
        font-size: 15px;
        
    }
    .avatar-review{
        width: .6cm;
        border-radius: 50px;
    }
}