.cat-home{
	width: auto;
	height: auto;
	border: none;
	color: whitesmoke;
	background-color: blueviolet;
	font-size: 20px;
	font-family: lora;
	letter-spacing: .03cm;
	// font-weight: 600;
	border-radius: 5px;
	padding: 5px 10px 5px 10px;
	display: flex;
	justify-content: center;
	align-items: center;
	box-shadow: rgba(0, 0, 0, 0.16) 0px 3px 6px, rgba(0, 0, 0, 0.23) 0px 3px 6px;
    // transition: 0.3s;
	animation: dict1 1.5s ease infinite;
	cursor: pointer;
}
.cat-home:hover{
	box-shadow: rgba(2, 0, 0, 0.978) 0px 0px 0px 2px;
}

